import React, { Fragment } from "react";
import aboutMe from "../img/aboutMe.svg";

const AboutMe = () => {
  return (
    <Fragment>
      <div className="h-16">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
          <path
            fill="#000000"
            fill-opacity="1"
            d="M0,32L1440,96L1440,0L0,0Z"
          ></path>
        </svg>
      </div>
      <div id="about_me" className="py-12">
        <div className="container px-6 mx-auto">
          <div className="h-screen flex flex-wrap justify-evenly items-center">
            <div className=" space-y-6 sm:w-1/2 ">
              <h1 className=" text-4xl py-2">I Introduce Myself...</h1>

              <p className="text-dark break-words font-normal text-lg ">
                Hello my name is Cristian and I am a software developer, a dream
                in which I have been working a little to become a true Full
                Stack Developer, nice to meet you, welcome to my professional
                and personal website, a space where you will meet a little of
                what I do.
              </p>
            </div>

            <img
              className="h-52 sm:h-80 bg-cover bg-no-repeat bg-center"
              src={aboutMe}
              alt="aboutMe"
            />
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default AboutMe;
