import React from "react";
import Background from "../img/banner.jpg";
import Typed from "react-typed";
import "../styles/HeroContentStyles.css";
import img_content from "../img/img_content.svg";
import img_mern_logo from "../img/mern_logo.png";

const HeroContent = () => {
  return (
    <div
      id=""
      className="bg-cover bg-center bg-fixed "
      style={{ backgroundImage: `url(${Background})` }}
    >
      <div className="flex flex-wrap items-center justify-evenly bg-gradient-to-t from-black h-screen">
        <div className="relative px-8 w-full sm:w-1/2">
          <div className="font-mono font-bold text-white text-center text-3xl space-y-6 sm:space-y-8">
            <h1 className="">Hello, I am a web developer and I work with: </h1>
            <Typed
              strings={[
                "HTML",
                "CSS",
                "JavaScript",
                "ReactJS",
                "NodeJS",
                "Express",
                "MongoDB",
              ]}
              typeSpeed={90}
              backSpeed={50}
              loop
            />
            <img
              className="h-20 lg:h-32 w-100 mx-auto"
              src={img_mern_logo}
              alt="img_mern"
            />
          </div>
        </div>

        {/* <img
          className="h-36 m-4 sm:h-56 w-full sm:w-1/3"
          src={img_content}
          alt="img_content"
        /> */}
      </div>
    </div>
  );
};

export default HeroContent;
