import React, { Fragment } from "react";
import { ProjectCard } from "./ProjectCard";
import project_one from "../img/project_one.png";
import project_two from "../img/project_two.png";

function Projects() {
  return (
    <Fragment>
      <div id="projects_section" className="py-16 bg-gray-100 sm:py-24">
        <h1 className="text-4xl font-normal text-black pb-4">My Projects</h1>
        <div className="p-8 sm:px-20 md:px-10 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-5 sm:gap-20">
          {/*Card*/}
          <ProjectCard
            title="My First Project"
            enlace={"enlace"}
            hastags={["Beginer", "React"]}
            urlImage={project_one}
          />
          {/*Card*/}
          <ProjectCard
            title="Inventory App"
            enlace={"enlace"}
            hastags={["React", "Firebase"]}
            urlImage={project_two}
          />
          <ProjectCard
            title="Inventory App"
            enlace={"enlace"}
            hastags={["React", "Firebase"]}
            urlImage={project_two}
          />
          <ProjectCard
            title="Inventory App"
            enlace={"enlace"}
            hastags={["React", "Firebase"]}
            urlImage={project_two}
          />
          <ProjectCard
            title="Inventory App"
            enlace={"enlace"}
            hastags={["React", "Firebase"]}
            urlImage={project_two}
          />
          <ProjectCard
            title="Inventory App"
            enlace={"enlace"}
            hastags={["React", "Firebase"]}
            urlImage={project_two}
          />
        </div>
      </div>
    </Fragment>
  );
}

export default Projects;
