import React from "react";

import github_logo from "../img/github_logo.png";

export const ProjectCard = ({ title, enlace, hastags, urlImage }) => {
  return (
    <>
      <div class="rounded bg-white overflow-hidden shadow-lg transform hover:-translate-y-1 hover:scale-104 duration-200">
        <img class="" src={urlImage} alt="Mountain" />
        <div class="p-2 w-100 mx-auto">
          <h1 class="font-bold text-sm mb-2">{title}</h1>
          <a href={enlace}>
            <img className="h-4 w-100 mx-auto" src={github_logo} alt="" />
          </a>
        </div>
        <div class="p-2">
          {hastags.map((hastag, index) => {
            return (
              <span
                key={index}
                class="inline-block bg-gray-200 rounded-full px-3 py-1 text-xs font-medium text-gray-700 mr-2 mb-2"
              >
                {hastag}
              </span>
            );
          })}
        </div>
      </div>
    </>
  );
};
