import React, { useState } from "react";
import logoCris from "../img/cris-blanco-logo.png";
import heroCris from "../img/here-foto.jpg";
import "../styles/NavBarStyle.css";

const Navbar = () => {
  const [toggle, setToggle] = useState(true);

  return (
    <div className="bg-black bg-opacity-90 p-2 sm:flex justify-between w-full fixed px-4 z-50 shadow-lg">
      <div className="flex justify-between items-center">
        <a href="#">
          <img
            className="fill-current text-white h-8 mt-1"
            src={logoCris}
            alt="personalLogo"
          />
        </a>
        <svg
          id="btnMenu"
          className="fill-current text-white mt-2 mr-2 cursor-pointer"
          width="24"
          onClick={() => setToggle(!toggle)}
          height="24"
          xmlns="http://www.w3.org/2000/svg"
          fillRule="evenodd"
          clipRule="evenodd"
        >
          <path
            d="M24 18v1h-24v-1h24zm0-6v1h-24v-1h24zm0-6v1h-24v-1h24z"
            fill="#1040e2"
          />
          <path d="M24 19h-24v-1h24v1zm0-6h-24v-1h24v1zm0-6h-24v-1h24v1z" />
        </svg>
      </div>
      <div
        id="list_btn"
        className="flex items-center z-0"
        // eslint-disable-next-line react/jsx-no-duplicate-props
        className={toggle ? "hidden" : ""}
      >
        <ul
          id="menuList"
          className="toogle-links text-white font-semibold text-center sm:flex w-full"
        >
          <li className="sm:ml-4 self-center">
            <a
              className="block no-underline p-2 text-center text-white transform hover:-translate-2 hover:scale-110 duration-300"
              href="#"
              onClick={() => setToggle(!toggle)}
            >
              Home
            </a>
          </li>
          <li className="sm:ml-4 self-center">
            <a
              className="block no-underline p-2 text-center text-white transform hover:-translate-2 hover:scale-110 duration-300"
              href="#about_me"
              onClick={() => setToggle(!toggle)}
            >
              AboutMe
            </a>
          </li>

          <li className="sm:ml-4 self-center">
            <a
              className="border-t no-underline sm:border-none block p-2 text-center text-white transform hover:-translate-2 hover:scale-110 duration-300"
              href="#projects_section"
              onClick={() => setToggle(!toggle)}
            >
              Projects
            </a>
          </li>
          <li className="sm:ml-4 self-center">
            <a
              className="border-t no-underline sm:border-none block p-2 text-center text-white transform hover:-translate-2 hover:scale-110 duration-300"
              href="#contact_content"
              onClick={() => setToggle(!toggle)}
            >
              Contact Me
            </a>
          </li>
          <li className="sm:ml-4 content-center">
            <img
              src={heroCris}
              alt="PerfilPotho"
              className="h-10 w-10 mb-3 text-center mx-auto rounded-3xl border-2"
            />
          </li>
        </ul>
      </div>
    </div>
  );
};

export default Navbar;
